:root {
  --background: linear-gradient(rgb(57, 57, 57), rgb(43, 43, 43));
  --navbar: linear-gradient(rgb(43, 43, 43), rgb(8, 8, 8));
  --nav-text: rgb(132, 132, 132);
  --nav-text-hover: rgb(255, 255, 255);
  --search-div: linear-gradient(rgb(57, 57, 57), rgb(43, 43, 43));
  --search-bar: rgb(229, 229, 229);
  --card-text: rgb(229, 229, 229);
  --card-background: rgb(64, 64, 64);
  --button-blue: rgb(93, 174, 208);
  --button-delete: rgb(69, 69, 73);
  --practice-day: rgb(123, 123, 123);
  --sidebar: rgb(50, 50, 50);
  --box-shadow: 
    0 0 1px rgba(0, 0, 0, 0.3), 
    0 3px 7px rgba(0, 0, 0, 0.3), 
    inset 0 1px rgb(104, 104, 104),
    inset 0 -3px 2px rgba(0,0,0,0.25);
  --box-shadow-active:
    0 0 10px rgba(173, 173, 173, 0.3), 
    0 10px 10px rgba(117, 117, 117, 0.3), 
    inset 0 1px rgba(219, 219, 219, 0.587),
    inset 0 -3px 10px rgba(34, 34, 34, 0.25);
  --border: rgb(38, 38, 38);
}


[data-theme='light'] {
  --background: rgb(255, 249, 231);
  --navbar: rgb(255, 237, 213);
  --nav-text: rgb(119, 56, 0);
