/* VARIABLES */

:root {
  --text-color: #777;
  --text-color-light: rgb(193, 16, 16);
  --bg-color: #654506;

}

/* GENERAL */

* {
  box-sizing: border-box;
}


header {
  display: block;
  width: 100%;
  padding: 1.75em 0;
  border-bottom: 1px solid var(--card-text);
  background: var(--search-div);
}

main {
  display: block;
  margin: 0 auto;
  margin-top: 5em;
  max-width: 50em;
}

/* GRID */

.row {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.row-middle {
  align-items: center;
}

.col {
  flex-grow: 1;
  flex-basis: 0;
  max-width: 100%;
}

.col-start {
  justify-content: flex-start;
  text-align: left;
}

.col-center {
  justify-content: center;
  text-align: center;
}

.col-end {
  justify-content: flex-end;
  text-align: right;
}

/* Calendar */

.calendar-div {
  top: 0px;
  padding-top: 70px;
  min-width: 550px;
}

.calendar-div > * {
  color: var(--card-text)
}

.calendar {
  display: block;
  position: relative;
  width: 90%;
  background: var(--search-div);
  border: 1px solid var(--card-text);
  border-radius: 50px;
}

.calendar .header {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 115%;
  padding: 1.5em 0;
  border-bottom: 1px solid var(--card-text);
}

.calendar .header .icon {
  cursor: pointer;
  transition: 0.15s ease-out;
}

.calendar .header .icon:hover {
  -webkit-transform: scale(1.75);
          transform: scale(1.75);
  transition: 0.25s ease-out;
  color: var(--card-text);
}

.calendar .header .icon:first-of-type {
  margin-left: 1em;
}

.calendar .header .icon:last-of-type {
  margin-right: 1em;
}

.calendar .days {
  text-transform: uppercase;
  font-weight: 400;
  color: var(--card-text);
  font-size: 70%;
  padding: 0.75em 0;
  border-bottom: 1px solid var(--card-text);
}

.calendar .body .cell {
  position: relative;
  height: 5em;
  border-right: 1px solid var(--card-text);
  overflow: hidden;
  background: var(--search-div);
  transition: 0.25s ease-out;
}

.calendar .body .cell:hover {
  background: var(--navbar);
  transition: 0.5s ease-out;
}

.calendar .body .selected {
  border-left: 10px solid transparent;
  border-image: linear-gradient(45deg, #1a8fff 0%, #53cbf1 40%);
  border-image-slice: 1;
}

.calendar .body .row {
  border-bottom: 1px solid var(--card-text);
}

.calendar .body .row:last-child {
  border-bottom: none;
}

.calendar .body .row:last-child :first-child {
  border-bottom-left-radius: 50px;
}

.calendar .body .row:last-child :last-child {
  border-bottom-right-radius: 50px;
}

.calendar .body .cell:last-child {
  border-right: none;
}

.calendar .body .cell .number {
  position: absolute;
  font-size: 82.5%;
  line-height: 1;
  top: 0.75em;
  right: 0.75em;
  font-weight: 700;
  color: var(--card-text);
}

.calendar .body .disabled {
  color: var(--card-text);
  pointer-events: none;
}

.calendar .body .cell .bg {
  font-weight: 700;
  line-height: 1;
  color: var(--card-text);
  opacity: 0;
  font-size: 8em;
  position: absolute;
  top: -0.2em;
  right: -0.05em;
  transition: 0.25s ease-out;
  letter-spacing: -0.07em;
}

.calendar .body .cell:hover .bg,
.calendar .body .selected .bg {
  opacity: 0.05;
  transition: 0.5s ease-in;
}

.calendar .body .col {
  flex-grow: 0;
  flex-basis: calc(100% / 7);
  width: calc(100% / 7);
}

.tooltip {
  position: relative;
  display: inline-block;
}

.tooltip .tooltiptext {
  visibility: hidden;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;

  /* Position the tooltip */
  position: absolute;
  z-index: 1;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
}
