/* ---------MODAL---------- */

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) scale(0);
          transform: translate(-50%, -50%) scale(0);
  transition: 200ms ease-in-out;
  border: 1px solid rgb(226, 239, 255);
  border-radius: 30px;
  z-index: 10;
  background-color: white;
  height: 400px;
  width: 200px;
  max-width: 70%;
}

.modal-active {
  -webkit-transform: translate(-50%, -50%) scale(1);
          transform: translate(-50%, -50%) scale(1);
  position: fixed;
  top: 40%;
  left: 50%;
  transition: 200ms ease-in-out;
  border: 1px solid var(--card-text);
  border-radius: 30px;
  z-index: 10;
  background-color: var(--card-background);
  color: var(--card-text);
  height: auto;
  width: 350px;
  text-align: center;
}

.modal-header {
  padding: 10px 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid var(--card-text);
}

.modal-header .title {
  font-size: 1.25rem;
  font-weight: bold;
}

.modal-header .close-button {
  cursor: pointer;
  border: none;
  outline: none;
  background: none;
  font-size: 1rem;
  font-weight: bold;
  color: var(--card-text);
}

.modal-header .close-button:hover {
  color: rgb(255, 45, 45);
}

#modal-submit {
  background-color: var(--button-blue)
}

#modal-submit:hover {
  background-color: rgb(68, 205, 255)
}

.modal-body {
  padding: 10px 15px;
}

#overlay {
  position: fixed;
  opacity: 0;
  transition: 200ms ease-in-out;
  top: 0;
  left: 0;
  right: 0;
  bottom:0;
  background-color: rgba(0, 0, 0, 0.64);
  pointer-events: none;

}

#overlay-active {
  opacity: .5;
  pointer-events: all;
  position: fixed;
  transition: 200ms ease-in-out;
  top: 0;
  left: 0;
  right: 0;
  bottom:0;
  background-color: rgb(0, 0, 0);
  z-index: 9;
}

